<template>
  <div>
    <mrc_ess_iconGroup :is_disabled="disabled"
                       :show_del='false'
                       @handleReturn='handleReturn'
                       @handleEdit='handleEdit'></mrc_ess_iconGroup>
    <base-info @success='success'
               :disabled='disabled'
               ref="baseInfo"
               :loading='submitLoading'
               :paramsData='params'
               @cancel='cancel'></base-info>
  </div>
</template>

<script>
import baseInfo from '../modules/baseInfo'
import { admin_account_info, admin_account_update } from '@/service/api/admin_account_manage'
export default {
  components: { baseInfo },
  props: {},
  data () {
    return {
      disabled: true,
      params: {},
      loading: false,
      submitLoading: false,
      unbind_staff_uuid: ''
    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {},
  async mounted () {
    await this.national('mrbase_sys_users')
    this.$nextTick(async () => {
      if (!this.$store.state.iconstatus) {
        this.disabled = false
      }
      this.loading = true
      const uuid = this.$route.params.uuid || this.$store.state.uuid
      const name = this.$store.getters.subPage_name
      this.$route.meta.name = name
      this.params.uuid = uuid
      await this.getInfo()
      this.loading = false
    })
  },

  methods: {
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    cancel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(async () => {
          this.disabled = !this.disabled
          await this.getInfo()
        }).catch(() => { })
    },
    async success (val) {
      let obj = {}
      if (val.staffs_uuid) {
        obj = Object.assign(
          {},
          { org_level_uuid: this.$store.state.userData.org_level_uuid },
          val)
      } else {
        obj = Object.assign(
          {},
          val, { unbind_staff_uuid: this.unbind_staff_uuid, org_level_uuid: this.$store.state.userData.org_level_uuid })

      }
      this.submitLoading = true
      const res = await admin_account_update(obj)
      if (res.code == 200) {
        this.$Message({ text: this.$t('message_The_operation_was_successful') })
        this.$router.go(-1)
      }
      this.submitLoading = false
    },
    async getInfo () {
      const res = await admin_account_info({
        uuid: this.params.uuid
      })
      if (res.code == 200) {
        this.unbind_staff_uuid = res.data.staffs_uuid
        const {
          status,
          user_account,
          user_pwd,
          user_primary_email,
          clients_uuid,
          user_name,
          staffs_uuid,
          uuid,
          org_level_uuid
        } = res.data
        this.params = Object.assign({}, {
          status,
          user_account,
          user_pwd,
          user_primary_email,
          clients_uuid,
          user_name,
          staffs_uuid,
          uuid,
          confirmPassword: '',
          org_level_uuid
        })
      }
    }
  },
};
</script>

