<template>
  <v-card class="pa-0"
          :elevation="0">
    <v-card-text>
      <v-row dense>
        <v-col class="d-flex"
               cols="12"
               sm="3">
          <mrc_ess_switch :true-value="1"
                          :false-value="0"
                          v-model="params.status"
                          :true-text="$t('label_To_enable_the')"
                          :readonly='disabled'
                          :false-text="$t('label_disable')"></mrc_ess_switch>
        </v-col>
      </v-row>
      <v-row>
        <!-- <v-col cols="12"
               class="d-flex"
               :sm="params.uuid ? 6 : 12">
          <mrc_ess_input :label="$t('label_The_login_name') + '*'"
                         :error-messages="$errors('params','user_account',requiredRule.concat(maxLength(20)))"
                         @input="$v.params.user_account.$touch()"
                         @blur="$v.params.user_account.$touch()"
                         :disabled='Boolean(params.staffs_uuid)'
                         v-model.trim="params.user_account"
                         placeholder='请输入登录名称'></mrc_ess_input>
        </v-col>
        <v-col cols="12"
               class="d-flex flex-column "
               sm="6">

          <mrc_ess_input :label="$t('label_password') + '*'"
                         type='password'
                         @input="$v.params.user_pwd.$touch()"
                         @blur="$v.params.user_pwd.$touch()"
                         v-model.trim="params.user_pwd"
                         placeholder='请输入密码'
                         :disabled='Boolean(params.staffs_uuid)'
                         :error-messages="$errors('params','user_pwd',requiredRule)"></mrc_ess_input>
          <span v-if="params.uuid"
                @click="resetPassword"
                style="color: #ff8a00;position:relative;top: 10px;cursor: pointer">重置密码</span>
        </v-col>
        <v-col cols="12"
               v-if="!params.uuid"
               class="d-flex flex-column "
               sm="6">

          <mrc_ess_input :label="$t('label_Confirm_password') + '*'"
                         type='password'
                         @input="$v.params.confirmPassword.$touch()"
                         @blur="$v.params.confirmPassword.$touch()"
                         v-model.trim="params.confirmPassword"
                         placeholder='请再次输入密码'
                         :disabled='Boolean(params.staffs_uuid)||Boolean(params.uuid)'
                         :error-messages="$errors('params','confirmPassword',requiredRule.concat(sameAsKey('密码')))"></mrc_ess_input>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="12">

          <mrc_ess_input label='电子邮箱*'
                         :error-messages="$errors('params','user_primary_email',requiredRule.concat(emailRule))"
                         @input="$v.params.user_primary_email.$touch()"
                         @blur="$v.params.user_primary_email.$touch()"
                         v-model.trim="params.user_primary_email"
                         :disabled='Boolean(params.staffs_uuid)'
                         placeholder='请输入电子邮箱'></mrc_ess_input>
        </v-col> -->
        <v-col cols="12"
               class="d-flex"
               :sm="params.uuid ? 6 : 12">
          <mrc_ess_multiText :label="$t('label_The_login_name') + '*'"
                             :error-messages="$errors('params','user_account',requiredRule.concat(maxLength(20)))"
                             :disabled='Boolean(params.staffs_uuid)'
                             v-model.trim="params.user_account"
                             style='width:100%'
                             :isNational="judegNational('user_account')"
                             placeholder='请输入登录名称'></mrc_ess_multiText>
        </v-col>
        <v-col cols="12"
               class="d-flex flex-column "
               sm="6">

          <mrc_ess_multiText :label="$t('label_password') + '*'"
                             type='password'
                             v-model.trim="params.user_pwd"
                             style='width:100%'
                             :isNational="judegNational('user_pwd')"
                             placeholder='请输入密码'
                             :disabled='Boolean(params.staffs_uuid)'
                             :error-messages="$errors('params','user_pwd',requiredRule)"></mrc_ess_multiText>
          <span v-if="params.uuid"
                @click="resetPassword"
                :style="{color: $vuetify.theme.defaults.light.primary,position:'relative',top: '10px',cursor: 'pointer'}">重置密码</span>
        </v-col>
        <v-col cols="12"
               v-if="!params.uuid"
               class="d-flex flex-column"
               sm="6">

          <mrc_ess_multiText :label="$t('label_Confirm_password') + '*'"
                             type='password'
                             v-model.trim="params.confirmPassword"
                             style='width:100%'
                             :isNational="judegNational('confirmPassword')"
                             :placeholder="$t('label_Confirm_password')"
                             :disabled='Boolean(params.staffs_uuid)||Boolean(params.uuid)'
                             :error-messages="$errors('params','confirmPassword',requiredRule.concat(sameAsKey('密码')))"></mrc_ess_multiText>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="12">

          <mrc_ess_multiText :label="$t('label_Mailbox')+'*'"
                             :error-messages="$errors('params','user_primary_email',requiredRule.concat(emailRule))"
                             v-model.trim="params.user_primary_email"
                             style='width:100%'
                             :isNational="judegNational('user_primary_email')"
                             :disabled='Boolean(params.staffs_uuid)'
                             :placeholder="$t('label_Mailbox')"></mrc_ess_multiText>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="12">
          <mrc_ess_prompt :obj='mrbase_sys_clients'
                          :label="$t('label_Associated_with_the_unit') + '*'"
                          multiple
                          @getSelectItem='getSelectClients'
                          :disabled='disabled'
                          :placeholder="$t('label_company_name')+'/'+$t('label_Short_code')"
                          :errMsg="$errors('params','org_level_uuid',requiredRule)"
                          v-model="computed_org_level_uuid"></mrc_ess_prompt>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="3">
          <v-checkbox dense
                      :readonly="disabled"
                      @change="isBindStaffChange"
                      v-model="isBindStaff"
                      :label="$t('label_Binding_employees')"></v-checkbox>

        </v-col>
        <v-col cols="12"
               sm="9"
               class="py-0">
          <mrc_ess_prompt :select_arr='bindStaffList'
                          @getSelectItem='getSelectItem'
                          v-show='isBindStaff'
                          :label="$t('label_Binding_employees')"
                          :disabled='disabled'
                          :rules='false'
                          :placeholder="$t('label_Binding_employees')"
                          v-model="params.staffs_uuid"></mrc_ess_prompt>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @cancel='cancel'
                       v-if="!disabled"
                       :loading='loading'
                       @success='success'
                       :cancelText="cancelText || $t('label_cancel')"
                       :successText="successText || $t('label_submit')"></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required, email, maxLength, sameAs } from 'vuelidate/lib/validators'
import { admin_account_reset } from '@/service/api/admin_account_manage'
import { getBusinessList } from '@/service/api/prompt'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    cancelText: {
      type: String
    },
    successText: {
      type: String
    },
    paramsData: {
      type: Object
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isBindStaff: false,
      params: {
        status: 1,
        user_account: '',
        user_pwd: '',
        user_primary_email: '',
        org_level_uuid: '',
        confirmPassword: '',
        staffs_uuid: '',
        client_uuid: ''
      },
      bindStaffList: []
    };
  },
  validations: {
    params: {
      confirmPassword: {
        required (val) {
          if (this.params.uuid) return true
          else return val.length > 0
        },
        sameAsKey (val) {
          if (this.params.uuid) return true
          else return val == this.params.user_pwd
        }
      },
      user_account: {
        required,
        maxLength: maxLength(20)
      },
      user_pwd: {
        required
      },
      user_primary_email: {
        required,
        email
      },
      org_level_uuid: {
        required
      }
    }
  },
  computed: {
    mrbase_sys_clients () {
      return {
        prompt_name: 'org_level_for_clients',
        client_uuid: this.$store.state.currentCompany
      }
    },
    computed_org_level_uuid: {
      get () {
        if (this.params.org_level_uuid) {
          return this.params.org_level_uuid.indexOf(',') > -1
            ?
            this.params.org_level_uuid.split(',')
            :
            [this.params.org_level_uuid]
        } else {
          return []
        }
      },
      set (val) {
        this.params.org_level_uuid = val.join()
      }
    }
  },
  created () { },
  watch: {
    paramsData: {
      handler (val) {
        this.params = val
        this.params.staffs_uuid ? this.isBindStaff = true : this.isBindStaff = false
      },
      deep: true
    }
  },
  async mounted () {
    if (this.paramsData) {
      this.params = this.paramsData
      this.params.staffs_uuid ? this.isBindStaff = true : this.isBindStaff = false
    }
    this.getPrompt()
  },

  methods: {
    getSelectClients (val) {
      let arr = []
      val.forEach(item => {
        if (arr.indexOf(item.f1) <= -1) {
          arr.push(item.f1)
        }
      })
      if (arr.length > 0) {
        this.params.client_uuid = arr.join()
      } else {
        this.params.client_uuid = ''
      }
    },
    isBindStaffChange (val) {
      if (!val) {
        this.params.user_account = ''
        this.params.user_pwd = ''
        this.params.confirmPassword = ''
        this.params.user_primary_email = ''
        this.params.staffs_uuid = ''
        this.$v.params.$reset()
      }
    },
    getSelectItem (val) {
      this.params.user_account = val.f4
      this.params.user_pwd = val.f5
      this.params.confirmPassword = val.f5
      this.params.user_primary_email = val.f6
    },
    async getPrompt () {
      const res = await getBusinessList(
        {
          prompt_name: 'levels-staffs',
          org_level_uuid: this.$store.state.currentCompany,
          staffs_uuid: this.params.staffs_uuid
        })
      if (res.code == 200) {
        this.bindStaffList = res.data
      }
    },
    success () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      if (!this.isBindStaff) {
        this.params.staffs_uuid = ''
      }
      this.$emit('success', this.params)
    },
    cancel () {
      this.$emit('cancel')
    },
    async resetPassword () {
      const reset = await admin_account_reset({ uuid: this.params.uuid })
      if (reset.code == 200) {
        this.$Message({ text: this.$t('message_The_operation_was_successful') })
      }
    }
  },
};
</script>

